import React, { useState } from 'react'
import './RecipeCard.css'

function Ingredient(props) {
  let component;

  const [ingredient, setIngredient] = useState(props.ingredient);

  const updateIngredient = (evt) => {
    evt.stopPropagation();
    
    setIngredient(prev => {
      const updated = { ...ingredient, [evt.target.name]: evt.target.value };
      // props.updateIngredient(prev, updated);
      return updated;
    });

    console.log(ingredient);
  }

  if (props.isEditing) {
    component = (
      <div className='ingredient-edit'>
        <div className='delete-icon' onClick={() => props.removeIngredient(ingredient.id)}>
          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" className="bi bi-x-circle" viewBox="0 0 16 16">
            <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z" />
            <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z" />
          </svg>
        </div>
        <input type='text' name='name' onChange={updateIngredient} value={ingredient.name} />
        <input type='text' name='quantity' onChange={updateIngredient} value={ingredient.quantity} />
        <input type='text' name='unit' onChange={updateIngredient} value={ingredient.unit} />
      </div>
    );
  } else {
    component = (
      <div className='ingredient'>
        <span className='name'>{ingredient.name}</span>
        <span className='quantity'>{ingredient.quantity} {ingredient.unit}</span>
      </div>
    );
  }

  return component;
}

export default Ingredient
